<template>
  <div class="enroll">
    <div>
      <b class="section-title">{{ competition.name }}</b>
    </div>
    <el-divider></el-divider>

    <div class="section">
      <TeamDescription ref="Team" title="队伍" operation="创建队伍" />
    </div>

    <div class="section">
      <MembersTable ref="Members" title="领队/教练" operation="添加成员" />
    </div>

    <div class="section">
      <AthletesTable ref="Athletes" title="运动员" operation="添加运动员" />
    </div>

    <el-divider></el-divider>
    <div class="bottom">
      <div class="button-options">
        报名人：<b>{{ userName }}</b>
      </div>
      <div class="button-options">联系方式：{{ userPhone }}</div>
      <el-button type="primary" @click="conformInfo">提交报名</el-button>
    </div>
  </div>
</template>

<script>
import { getDetail } from "../api/competitions";
import { getters } from "../store/store";

import TeamDescription from "@/components/TeamDescription.vue";
import MembersTable from "@/components/MembersTable.vue";
import AthletesTable from "@/components/AthletesTable.vue";

export default {
  name: "Enroll",
  components: { TeamDescription, MembersTable, AthletesTable },
  computed: {
    userPhone: getters.userPhone,
    userName: getters.userName,
  },
  data() {
    return {
      competition: "",
      forms: ["Team", "Members", "Athletes"],
    };
  },
  created() {
    const id = this.$route.query.id;
    if (id) {
      getDetail(id).then((data) => {
        this.competition = data;
      });
    }
    else {
      this.$router.push({
        name: "competitions",
      });
    }
  },
  methods: {
    extractData() {
      let enroll = {
        competition: this.competition.id,
        user: {
          name: this.userName,
          phone: this.userPhone,
        },
      };
      this.forms.map((ref) => {
        enroll[ref] = this.$refs[ref].getData();
      });
      return enroll;
    },
    conformInfo() {
      this.extractData();
      this.$confirm("您将提交本次报名的信息", "提交报名信息", {
        distinguishCancelAndClose: true,
        confirmButtonText: "正式提交",
        cancelButtonText: "我再改改",
      }).then(() => {
        this.submitEnroll()
      });
    },
    submitEnroll() {
      this.$message({
        type: "success",
        message: "提交成功",
      });
    },
  },
};
</script>

<style scoped>
.enroll {
  padding: 20px;
}

.section {
  min-height: 300px;
}

.section-title {
  font-size: 20px;
}

.bottom {
  display: flex;
  line-height: 40px;
}

.button-options {
  flex: 1;
}
</style>